<template>
    <div class="head_div">
        <div class="logo_div">
            <img src="/static/img/logo.jpg">
        </div>
        <div class="menu_div">
            <div>
                <el-menu
                    :default-active="activeIndex"
                    class="el-menu-demo"
                    mode="horizontal"
                    @select="handleSelect"
                    background-color="#FFFFFF"
                    text-color="#000000"
                    active-text-color="#FFFFFF">
                    <el-menu-item index="1">首页</el-menu-item>
                    <el-submenu index="2">
                        <template slot="title">我的工作台</template>
                        <el-menu-item index="2-1" @click="showMyPage">选项1</el-menu-item>
                        <el-menu-item index="2-2">选项2</el-menu-item>
                        <el-menu-item index="2-3">选项3</el-menu-item>
                        <el-submenu index="2-4">
                            <template slot="title">选项4</template>
                            <el-menu-item index="2-4-1">选项1</el-menu-item>
                            <el-menu-item index="2-4-2">选项2</el-menu-item>
                            <el-menu-item index="2-4-3">选项3</el-menu-item>
                        </el-submenu>
                    </el-submenu>
                    <el-menu-item index="3">消息中心</el-menu-item>
                    <el-menu-item index="4"><a href="https://nidegexing.github.io" target="_blank">我的Blog</a>
                    </el-menu-item>
                </el-menu>
            </div>
            <div class="user_div" @click="knockOn">USER</div>
        </div>
    </div>
</template>

<script>
    import apis from '../../apis'

    export default {
        name: "NavMenu",
        data() {
            return {
                activeIndex: '1',
                knockTimes: 0
            };
        },
        methods: {
            handleSelect(key, keyPath) {
                console.log(key, keyPath);
            },
            knockOn() {
                let that = this;
                this.knockTimes += 1;
                setTimeout(function () {
                    that.knockTimes = 0;
                }, 1500);
                if (this.knockTimes > 2) {
                    let loginData = {userName: 'kaola', pwd: 'jiaozhu'};
                    this.login(loginData);
                }
            },
            login(loginData) {
                //console.log('kaola--open the door');
                //根据api接口获取token
                apis.userLogin(loginData).then(res => {
                    this.$message.success('登录成功');
                    let data = res.data;
                    //console.log(data.result)
                    //根据store中set_token方法将token保存至localStorage/sessionStorage中，
                    //this.$store.commit('set_token', data["Authentication-Token"]);
                    this.$store.commit('set_token', data["result"]);

                    if (this.$store.state.token) {
                        this.$router.push('/')
                    } else {
                        this.$router.replace('/login');
                    }
                }).catch(error => {
                    console.log(error)
                    this.$message.error('登陆异常')
                })
            },
            showMyPage() {
                apis.getUser({userName: 'kaola'}).then(res => {
                    console.log(res);
                }).catch(error => {
                    console.log(error)
                });
            }
        }
    }
</script>

<style>
    .is-active .el-submenu__title {
        background-color: #000000 !important;
    }
</style>

<style scoped>
    .head_div {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;

        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        align-content: flex-start;

        min-width: 1024px;
        margin-right: auto;
        margin-left: auto;
    }

    .logo_div {
        position: relative;
        width: 240px;
        height: 126px;
        background: lightcyan;

    }

    .logo_div img {
        width: 100%;
    }

    .menu_div {
        position: relative;
        height: 60px;
        width: 73%;
        display: flex;
        justify-content: space-between;
        background: lightgreen;
    }

    .el-menu-demo .is-active {
        background-color: #000000 !important;
    }

    .el-menu--horizontal {
        border: none;
    }

    .user_div {
        position: relative;
        width: 180px;
        height: 60px;
        margin-right: 10%;
        background: lightblue;
    }
</style>



