[图片放大缩小](https://segmentfault.com/q/1010000012038536)
[ElementUI](https://www.jb51.net/article/142638.htm)
<template>
    <div class="container">
        <div class="pic_div" v-for="(img,index) in imgList" :key="img.id"
             :class="{scale: img.isScale}" @mouseover="img.isScale=true" @mouseout="img.isScale=false">
            <router-link :to="{name: img.link,params :{id:img.id}}">
                <img :src="img.src" :title="img.title">
            </router-link>
        </div>
    </div>
</template>

<script>
    export default {
        name: "BigPic",
        data() {
            return {
                imgList: [{
                    id: 1,
                    src: "/static/img/batman.jpg",
                    title: "TableView",
                    isScale: false
                }, {
                    id: 2,
                    src: "/static/img/batman.jpg",
                    isScale: false
                }, {
                    id: 3,
                    src: "/static/img/batman.jpg",
                    isScale: false
                }, {
                    id: 4,
                    src: "/static/img/batman.jpg",
                    isScale: false
                }, {
                    id: 5,
                    src: "/static/img/batman.jpg",
                    isScale: false
                }, {
                    id: 6,
                    src: "/static/img/batman.jpg",
                    isScale: false
                }, {
                    id: 7,
                    src: "/static/img/batman.jpg",
                    isScale: false
                }, {
                    id: 8,
                    src: "/static/img/batman.jpg",
                    isScale: false
                }]
            }
        }
    }
</script>

<!--图片自适应-->
<!--1.只需通过css设置background-size属性为contain，即-->
<!--background-size:contain-->
<!--2.如下-->
<!--https://blog.csdn.net/zh_rey/article/details/69666232-->
<style scoped>
    .container {
        position: relative;
        width: 600px;
        height: 600px;
        margin: 0;
        /*border: 1px solid blue;*/

        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        align-content: flex-start;
    }

    .pic_div {
        width: 134px;
        height: 90px;
        margin: 4px 0 0 4px;

        transform: scale(1);
        transition: all ease 0.2s;
    }

    .scale {
        transform: scale(1.5);
        z-index: 10;
    }

    img {
        position: relative;
        width: 100%;
        height: 100%;
        margin: 5px;
    }
</style>



