<template>
    <el-container>
        <el-main>
            <div class="main-part">
                <div class="left-item">
                    <pic-boxs></pic-boxs>
                </div>
                <div class="right-item">
                    right-part
                </div>
            </div>
            <div class="main-part mg-tp-1">
                <div class="left-item">
                    <pic-boxs></pic-boxs>
                </div>
                <div class="right-item">
                    right-part
                </div>
            </div>
        </el-main>
        <el-aside width="260px" v-if="showAside">
            <div></div>
        </el-aside>
    </el-container>
</template>

<script>

    import PicBoxs from './dynamicPic/BigPic'

    export default {
        name: 'HelloWorld',
        components: {
            PicBoxs
        },
        data() {
            return {
                fullWidth: document.documentElement.clientWidth
            }
        },
        mounted() {
            const that = this
            //监听window.onresize事件
            window.onresize = () => {
                return (() => {
                    window.fullWidth = document.documentElement.clientWidth

                    //0.5秒内不重复触发
                    if (!that.resizing) {
                        that.resizing = true
                        setTimeout(function () {
                            that.resizing = false
                            that.fullWidth = window.fullWidth
                        }, 1000)
                    }
                })()
            }
        },
        computed: {
            showAside() {
                if (this.fullWidth < 1200) {
                    return false;
                } else {
                    return true;
                }
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-main {
        /*background-color: #E9EEF3;*/
        color: #333;
        text-align: center;
    }

    .main-part {
        position: relative;
        width: 100%;
        height: 600px;
        overflow: hidden;

        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
    }

    .mg-tp-1 {
        margin-top: 1%;
    }

    .main-part .left-item {
        position: relative;
        width: 560px;
        height: 100%;

        background: darkkhaki;
    }

    .main-part .right-item {
        flex: 1;
        height: 100%;
        margin-left: 1%;

        background: #ffa7b8;
    }

    .el-aside {
        padding: 20px;
        text-align: center;
    }

    .el-aside div {
        position: relative;
        width: 100%;
        height: 100%;
        background: lightgreen;
    }
</style>



