<template>
    <div class='container'>
        <div class="head">Thi is a flex layout, enjoy it</div>
        <div class="bod">
            <component v-bind:is="currentTabComponent"></component>
        </div>
        <div class="foot">
            <span>foot</span>
            <div>
                <el-row>
                    <el-button @click="butClick('StaticCenter')">StaticCenter</el-button>
                    <el-button type="info" @click="butClick('DynamicCenter')">DynamicCenter</el-button>
                    <el-button type="danger" @click="butClick('LayoutBox')">多DIV布局</el-button>
                    <el-button type="primary" @click="butClick('LayoutFlex')">Flex</el-button>
                    <el-button type="success" @click="butClick('MyDice')">阮一峰的骰子</el-button>
                    <el-button type="warning" @click="butClick('GanttProgress')">甘特图</el-button>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
    import StaticCenter from '../components/layout/StaticCenter'
    import LayoutFlex from '../components/layout/LayoutFlex.vue'
    import MyDice from '../components/layout/DiceDemo.vue'
    import DynamicCenter from '../components/layout/DynamicCenter.vue'
    import GanttProgress from '../components/echarts/GanttProgress'
    import LayoutBox from '../components/layout/LayoutBox'

    // 使用Vue.component()注册组件，和 components 是同样的效果
    // Vue.component('tab-staticcenter', StaticCenter)

    export default {
        name: 'table-view',

        components: {
            // 'center-demo': StaticCenter,
            StaticCenter,
            DynamicCenter,
            LayoutFlex,
            MyDice,
            GanttProgress,
            LayoutBox
        },
        data: function () {
            return {
                showType: 'StaticCenter'
            };
        },
        methods: {
            butClick(data) {
                this.showType = data;
            }
        },
        computed: {
            currentTabComponent() {
                return this.showType;
            }
        }
    }
</script>

<style scoped="scoped">
    .container {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border: 1px solid #FFFF00;
    }

    .head {
        background-color: #9EEA6A;
        position: absolute;
        top: 0;
        width: 100%;
        height: 100px;
        overflow: hidden;
    }

    .bod {
        background-color: skyblue;
        position: absolute;
        top: 100px;
        bottom: 150px;
        width: 100%;
        overflow: hidden;
    }

    .foot {
        position: absolute;
        bottom: 0;
        height: 150px;
        width: 100%;
        background-color: palevioletred;
    }
</style>



