<!-- 布局参考：https://www.cnblogs.com/zhanghuiyun/p/6366034.html -->
<!-- 本源的力量：阮一峰大神，接受我的膜拜吧：http://www.ruanyifeng.com/blog/2015/07/flex-grammar.html?utm_source=tuicool -->
<!--设为 Flex 布局以后，子元素的float、clear和vertical-align属性将失效。-->
<template>
    <div class="mainDiv">
        <div class="itemBasic"></div>
        <div class="itemBasic2"></div>
        <div class="itemBasic"></div>
        <div class="itemBasic"></div>
        <div class="itemBasic2"></div>
        <div class="itemBasic"></div>
        <div class="itemBasic2"></div>
        <div class="itemBasic2"></div>
        <div class="itemBasic"></div>
        <div class="itemBasic"></div>
        <div class="itemBasic2"></div>
        <div class="itemBasic"></div>
        <div class="itemBasic2"></div>
    </div>
</template>

<script>
    export default {
        name: 'layout-flex',
        components: {},
        data: function () {
            return {}
        },
        methods: {}
    }
</script>

<style scoped>
    .mainDiv {
        width: 100%;
        height: 100%;
        background-color: #C0C0C0;

        display: flex;
        /* 容器内项目的排列方向(默认横向排列)*/
        /* {flex-direction: row | row-reverse | column | column-reverse;} */
        /* row(默认)沿水平主轴由左向右排列、row-reverse沿水平主轴由右向左排列、column沿垂直主轴右上到下和column-reverse。 */
        flex-direction: row;

        /* 容器内项目换行方式 */
        /* {flex-wrap: nowrap | wrap | wrap-reverse;} */
        /* nowrap(默认)不换行、wrap换行（第一行在上方）和wrap-reverse（你懂的~） */
        flex-wrap: wrap;

        /* 以上两个属性的简写方式 */
        /* {flex-flow: <flex-direction> || <flex-wrap>;} */
        /* flex-flow */

        /* 项目在主轴上的对齐方式 */
        /* {justify-content: flex-start | flex-end | center | space-between | space-around;} */
        /*项目在主轴上的对齐方式(主轴究竟是哪个轴要看属性flex-direction的设置了)*/
        /*flex-start：在主轴上由左或者上开始排列 flex-end：在主轴上由右或者下开始排列*/
        /*center：在主轴上居中排列*/
        /*space-between：在主轴上左右两端或者上下两端开始排列*/
        /*space-around：每个项目两侧的间隔相等。所以，项目之间的间隔比项目与边框的间隔大一倍。*/
        justify-content: flex-start;

        /* 项目在交叉轴上如何对齐 */
        /* {align-items: flex-start | flex-end | center | baseline | stretch;} */
        align-items: flex-start;

        /* 定义了多根轴线的对齐方式。如果项目只有一根轴线，该属性不起作用。*/
        /* {align-content: flex-start | flex-end | center | space-between | space-around | stretch;} */
        /*flex-start：与交叉轴的起点对齐。*/
        /*flex-end：与交叉轴的终点对齐。*/
        /*center：与交叉轴的中点对齐。*/
        /*space-between：与交叉轴两端对齐，轴线之间的间隔平均分布。*/
        /*space-around：每根轴线两侧的间隔都相等。所以，轴线之间的间隔比轴线与边框的间隔大一倍。*/
        /*stretch（默认值）：轴线占满整个交叉轴。*/
        align-content: flex-start;
    }

    .itemBasic {
        margin: 10px;
        width: 100px;
        height: 30px;
        background-color: coral;
    }

    .itemBasic2 {
        margin: 10px;
        width: 200px;
        height: 30px;
        background-color: coral;
    }
</style>



