<!--https://www.cnblogs.com/oneplace/p/5264052.html-->
<template>
    <div class="container">
        <div v-for="(item,index) in items" :key="item.id" class="item"
             :class="{ yellow: index%3 == 1, green: index%3 == 2}">
            <div v-if="item.id == 1" @click="showDesc(item.desc)" class="center-item ver-center">点我查看CSS</div>
            <div v-else-if="item.id == 2" @click="showDesc(item.desc)" class="center-item ver-center2">点我查看CSS</div>
            <div v-else-if="item.id == 3" @click="showDesc(item.desc)" class="center-item ver-center">点我查看CSS</div>
            <div v-else-if="item.id == 4" @click="showDesc(item.desc)" class="center-item ver-center2">点我查看CSS</div>
            <div v-else style="width: 100%;height: 100%;background-color: cyan" v-html="cssDesc"></div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                cssDesc: "CSS:未知高度垂直居中",
                items: [
                    {
                        id: 1,
                        desc: "未知高度垂直居中<br>" +
                            "top:50%;/*相对祖先元素向下移动50%*/<br>" +
                            "transform: translateY(-50%);/*元素y轴移动自身的一半*/<br>" +
                            "transform: translate(0,-50%);/*元素y轴移动自身的一半,与上一行效果相同*/"
                    }, {
                        id: 2,
                        desc: "未知高度垂直居中<br>" +
                            "/*首先是父元素与子元素的position属性值要“配套”好，这样子元素的top,bottom才可以发挥应有的作用*/<br>" +
                            "position: absolute;<br>" +
                            "top:30px;<br>" +
                            "bottom:30px;<br>" +
                            "/*当垂直方向上的margin为auto时，不论是margin-left，margin-right,还是margin:auto, auto总是会试图充满整个父元素，*/<br>" +
                            "margin: auto 10px;"
                    }, {
                        id: 5,
                        desc: "未知高度垂直居中"
                    }
                ]
            }
        },
        methods: {
            showDesc(desc) {
                this.cssDesc = desc;
            }
        }
    }
</script>

<style scoped>
    .container {
        width: 100%;
        height: 100%;
        background-color: #CCCCCC;

        position: relative;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .item {
        position: relative;
        width: 300px;
        height: 200px;
        background-color: grey;
        margin: 10px;
        text-align: left;
    }

    .center-item {
        position: relative;
        width: 100px;
        height: 60px;
        background-color: #211780;
        margin: 10px;
        color: white;
    }

    .yellow {
        background-color: yellow;
    }

    .green {
        background-color: #d944ff;
    }

    .ver-center {
        top: 50%; /*相对祖先元素向下移动50%*/
        transform: translateY(-50%); /*元素y轴移动自身的一半*/
        transform: translate(0, -50%); /*元素y轴移动自身的一半,与上一行效果相同*/
    }

    .ver-center2 {
        /*首先是父元素与子元素的position属性值要“配套”好，这样子元素的top,bottom才可以发挥应有的作用*/
        position: absolute;
        top: 30px;
        bottom: 30px;
        /*当垂直方向上的margin为auto时，不论是margin-left，margin-right,还是margin:auto, auto总是会试图充满整个父元素，*/
        margin: auto 10px;
    }
</style>



