<template>
    <div class="mainDiv">
        <div class="box-container">
            <div>水平排列<br>
                父元素：text-align:center;<br>
                子元素：display:inline-block;
            </div>
            <div class="box-1"></div>
            <div class="box-1"></div>
            <div class="box-1"></div>
            <div class="box-1"></div>
        </div>
        <div class="box-container">
            <div>左边固定，右边自适应</div>
            <div class="box-container-2">
                <div class="box2-left"></div>
                <div class="box2-right">
                    <div class="box-1"></div>
                    <div class="box-1"></div>
                    <div class="box-1"></div>
                    <div class="box-1"></div>
                </div>
            </div>
        </div>
        <div class="box-container">box3</div>
        <div class="box-container">box4</div>
    </div>
</template>

<script>
    export default {
        name: "LayoutBox"
    }
</script>

<style scoped>
    .mainDiv {
        position: relative;
        width: 100%;
        height: 100%;
        background-color: #C0C0C0;

        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items: flex-start;
    }

    .box-container {
        width: 50%;
        height: 50%;
        background: lightpink;
    }

    .box-container:nth-of-type(2) {
        background: #cfffdd;
    }

    .box-container:nth-of-type(3) {
        background: lightseagreen;
    }

    .box-container:nth-of-type(4) {
        background: lightskyblue;
    }

    .box-1 {
        position: relative;
        width: 60px;
        height: 60px;
        margin: 1%;
        background: lightsteelblue;

        display: inline-block;
    }

    .box-container-2 {
        position: relative;
        width: 100%;
        height: 80%;
        background: lightgray;

        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        align-items: flex-start;

        overflow: hidden;
    }

    .box2-left {
        width: 200px;
        height: 100%;
        background: lightcyan;
    }

    .box2-right {
        flex: 1; /*精髓，妙不可言：flex为1，自动填充剩下的区域*/
        height: 100%;
        background: aquamarine;
    }

    .box-container-2 .box-1 {
        height: 100px;
    }
</style>



