<template>
    <div id="app">
        <el-container>
            <el-header height="126px" v-if="showHead">
                <head-menu></head-menu>
            </el-header>
            <div>
                <router-view/>
            </div>
            <el-footer height="110px" v-if="showHead">
                <main-foot></main-foot>
            </el-footer>
        </el-container>
    </div>
</template>

<script>
    import HeadMenu from './components/navMenu/NavMenu'
    import MainFoot from './components/foot/MainFoot'

    export default {
        name: 'App',
        components: {
            MainFoot,
            HeadMenu
        },
        data() {
            return {}
        },
        computed: {
            showHead() {
                if (this.$route.params.debugMode) {
                    return false;
                } else {
                    return true;
                }
            }
        }
    }
</script>

<style>
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
    }

    /*适应头部标题栏，设定最小宽度*/
    body {
        min-width: 1024px;
    }

    .el-header, .el-footer {
        position: relative;
        text-align: center;
        background: lightcoral;
    }
</style>



