<!--https://www.cnblogs.com/oneplace/p/5264052.html-->
<template>
    <div class="container">
        <div v-for="(item,index) in items" :key="item.id" class="item"
             :class="{ yellow: index%3 == 1, green: index%3 == 2}">
            <div v-if="item.id == 1" @click="showDesc(item.desc)" class="center-item hor-center">点我查看CSS</div>
            <div v-else-if="item.id == 2" @click="showDesc(item.desc)" class="center-item hor-center2">点我查看CSS</div>
            <div v-else-if="item.id == 3" @click="showDesc(item.desc)" class="center-item ver-center">点我查看CSS</div>
            <div v-else-if="item.id == 4" @click="showDesc(item.desc)" class="center-item ver-center2">点我查看CSS</div>
            <div v-else style="width: 100%;height: 100%;background-color: cyan" v-html="cssDesc"></div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                cssDesc: "CSS：块级元素居中",
                items: [
                    {
                        id: 1,
                        desc: "块级元素水平居中<br>margin: 0 auto;"
                    }, {
                        id: 2,
                        desc: "块级元素水平居中<br>" +
                            "/*绝对定位*/<br>" +
                            "position: relative;<br>" +
                            "left: 50%;<br>" +
                            "/*负边距：自身宽度一半 */<br>" +
                            "margin-left: -50px;"
                    }, {
                        id: 3,
                        desc: "/*元素相对其最近的position属性不为static的元素(祖先元素)移动50%，<br>" +
                            "top、right、bottom 和 left 属性指定定位元素的位置。*/<br>" +
                            "top: 50%;<br>" +
                            "/*元素向上移动自身的50%，此时，正好垂直居中*/<br>" +
                            "margin-top: -30px;"
                    },
                    {
                        id: 4,
                        desc: "top:50%;<br>" +
                            "/*相对于自身高度，向上移动50%*/<br>" +
                            "transform: translateY(-50%);"
                    },
                    {
                        id: 5,
                        desc: "块级元素水平居中"
                    }
                ]
            }
        },
        methods: {
            showDesc(desc) {
                this.cssDesc = desc;
            }
        }
    }
</script>

<style scoped>
    .container {
        width: 100%;
        height: 100%;
        background-color: #CCCCCC;

        position: relative;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }

    .item {
        position: relative;
        width: 300px;
        height: 200px;
        background-color: grey;
        margin: 10px;
        text-align: left;
    }

    .center-item {
        position: relative;
        width: 100px;
        height: 60px;
        background-color: #211780;
        margin: 10px;
        color: white;
    }

    .yellow {
        background-color: yellow;
    }

    .green {
        background-color: #d944ff;
    }

    .hor-center {
        margin: 0 auto;
    }

    .hor-center2 {
        /*绝对定位*/
        position: relative;
        left: 50%;
        /*负边距：自身宽度一半 */
        margin-left: -50px;
    }

    .ver-center {
        /*元素相对其最近的position属性不为static的元素(祖先元素)移动50%，
        top、right、bottom 和 left 属性指定定位元素的位置。*/
        top: 50%;
        /*元素向上移动自身的50%，此时，正好垂直居中*/
        margin-top: -30px;
    }

    .ver-center2 {
        top: 50%;
        /*相对于自身高度，向上移动50%*/
        transform: translateY(-30px);
    }
</style>



