<!--文章很好，就是没有说明怎么使用，我这里解析一下
https://www.cnblogs.com/wisewrong/p/6558001.html-->
<template>
    <div class='container'>
        <div ref="mychart" class="echartDiv"></div>
    </div>
</template>

<script>
    import echarts from 'echarts'
    import * as dateUtil from '../../../static/js/DateUtil.js'

    export default {
        name: 'table-view',
        data: function () {
            return {
                // myChart实例
                myChart: {},
                echartsOption: {
                    title: {
                        text: 'VUE学习进度表',
                        subtext: 'From KaolaHome'
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                            type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                        },
                        formatter: function (params) {
                            /*
                            * 弹出tip的数据格式化
                            * 传入的是，当前阶段的数据：本例来说，就是四个item的值
                            * 计算方式说明：每个环节对于series中的data
                            * 环节一，data[0]分别为0，1，0，1
                            * 环节二，data[1]分别为2，1，2，1
                            * 环节三，data[2]分别为4，8，4，9
                            * 环节四，data[3]分别为12，1，13，1
                            * 环节五，data[4]分别为13，30，13，27
                            * 环节六，data[5]44，7，41，5
                            * 环节七，data[5]分别为52，29，47，25
                            * */
                            var planStartData = {
                                '环节一': '2017-11-01',
                                '环节二': '2017-11-03',
                                '环节三': '2017-11-05',
                                '环节四': '2017-11-13',
                                '环节五': '2017-11-14',
                                '环节六': '2017-12-15',
                                '环节七': '2017-12-23'
                            };
                            var planEndData = {
                                '环节一': '2017-11-01',
                                '环节二': '2017-11-03',
                                '环节三': '2017-11-12',
                                '环节四': '2017-11-13',
                                '环节五': '2017-12-13',
                                '环节六': '2017-12-21',
                                '环节七': '2018-01-20'
                            };
                            var actualStartData = {
                                '环节一': '2017-11-01',
                                '环节二': '2017-11-03',
                                '环节三': '2017-11-05',
                                '环节四': '2017-11-14',
                                '环节五': '2017-11-15',
                                '环节六': '2017-12-12',
                                '环节七': '2017-12-18'
                            };
                            var actualEndData = {
                                '环节一': '2017-11-01',
                                '环节二': '2017-11-03',
                                '环节三': '2017-11-13',
                                '环节四': '2017-11-14',
                                '环节五': '2017-12-10',
                                '环节六': '2017-12-16',
                                '环节七': '2018-12-11'
                            };
                            var planItem = params[1];
                            var actualItem = params[3];
                            var planText = "计划时间：" + planStartData[planItem.name] + "~" + planEndData[planItem.name];
                            var actualText = "实际时间：" + actualStartData[planItem.name] + "~" + actualEndData[planItem.name];
                            return planItem.name + '<br/>' + planText + '<br/>' + actualText + '<br/>' + planItem.seriesName + ' : ' + planItem.value + '<br/>' + actualItem.seriesName + ' : ' + actualItem.value;
                        }
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    yAxis: {
                        type: 'category',
                        splitLine: {show: false},
                        data: ['环节一', '环节二', '环节三', '环节四', '环节五', '环节六', '环节七']
                    },
                    xAxis: {
                        type: 'value',
                        axisLabel: {
                            textStyle: {       // 其余属性默认使用全局文本样式，详见TEXTSTYLE
                                color: 'black',
                                fontSize: 10,
                            },
                            // 使用函数模板，函数参数分别为刻度数值（类目），刻度的索引
                            formatter: function (value, i) {
                                return dateUtil.dateOperator('2017-11-01', value, '+');
                            }
                        }
                    },
                    series: [
                        /*
                        *   四组数据，
                        *   第一组表示计划时间前的占位
                        *   第二组表示计划时间
                        *   第三组表示实际时间前的占位
                        *   第四组表示实际时间
                        *   修改参数说明：
                        *   stack：相当于数据分类，配置相同的stack值可以堆叠放置
                        *   data：每个环节占用长度
                        * */
                        {
                            name: '占位1',
                            type: 'bar',
                            stack: '计划',
                            itemStyle: {
                                normal: {
                                    barBorderColor: 'rgba(0,0,0,0)',
                                    color: 'rgba(0,0,0,0)'              //改为1，可显示date
                                },
                                emphasis: {
                                    barBorderColor: 'rgba(0,0,0,0)',
                                    color: 'rgba(0,0,0,0)'
                                }
                            },
                            data: [0, 2, 4, 12, 13, 44, 52]
                        },
                        {
                            name: '计划工期',
                            type: 'bar',
                            stack: '计划',
                            label: {
                                normal: {
                                    show: true,
                                    position: 'inside'
                                }
                            },
                            data: [1, 1, 8, 1, 30, 7, 29]
                        },
                        {
                            name: '占位2',
                            type: 'bar',
                            stack: '实际',
                            itemStyle: {
                                normal: {
                                    barBorderColor: 'rgba(0,0,0,0)',
                                    color: 'rgba(0,0,0,0)'
                                },
                                emphasis: {
                                    barBorderColor: 'rgba(0,0,0,0)',
                                    color: 'rgba(0,0,0,0)'
                                }
                            },
                            data: [0, 2, 4, 13, 13, 41, 47]
                        },
                        {
                            name: '实际工期',
                            type: 'bar',
                            stack: '实际',
                            label: {
                                normal: {
                                    show: true,
                                    position: 'inside'
                                }
                            },
                            data: [1, 1, 9, 1, 27, 5, 25]
                        }
                    ]
                }
            }
        },
        mounted() {
            // let myChart = echarts.init(document.getElementById('myChart'))
            // myChart.setOption(this.echartsOption);

            let dom = this.$refs.mychart;
            this.myChart = echarts.init(dom);
            this.myChart.setOption(this.echartsOption);
        }
    }
</script>

<style scoped="scoped">
    .container {
        width: 100%;
        height: 100%;
        background-color: #FFFFFF;

        position: relative;
    }

    .echartDiv {
        width: 900px;
        height: 400px;
        border: 1px red solid;

        position: absolute;
        top: 30px;
        bottom: 30px;
        /*当垂直方向上的margin为auto时，不论是margin-left，margin-right,还是margin:auto, auto总是会试图充满整个父元素，*/
        margin: auto 100px;
    }
</style>



